<template>
  <div class="row no-gutters">
    <div class="col-9">
      <div class="form-group">
        <select class="form-control" :id="field_id">
          <option
            v-for="behaviour_list in behaviour_lists"
            v-bind:key="behaviour_list.id"
            :value="behaviour_list.id"
          >{{behaviour_list.caption}}</option>
        </select>
      </div>
    </div>
    <div class="col-3 pl-1">
      <a role="button" class="btn btn-danger btn-block" title='Add' @click="add">&#x271A;</a>
    </div>
  </div>
</template>

<script>
export default {
  props: [
    "behaviour_lists",
    "data_id",
    "_negative_id_couner",
    "page_object_params"
  ],
  created() {
    //
  },
  mounted() {
    if (typeof this.negative_id_couner == "undefined")
      this.negative_id_couner = -1;
  },
  data() {
    return {
      negative_id_couner: this._negative_id_couner,
      field_id: "object_" + this.data_id + "_behaviour_add"
    };
  },
  methods: {
    addByListId(behaviour_list_id) {
      const result = this.page_object_params.add_behaviour({
        id: this.negative_id_couner,
        behaviour_list_id,
        target_page_object_id: this.data_id
      });
      this.negative_id_couner--;
      return result;
    },
    add() {
      const behaviour_list_id = document.getElementById(this.field_id).value;
      return this.addByListId(behaviour_list_id);
    }
  }
};
</script>
