<template>
  <div>
    <input
      type="number"
      :class="'drag-number12 ' + _class"
      :min="view_min"
      :max="view_max"
      :step="view_step"
      :value="view_value"
      @mousedown.prevent="mousedown"
      @touchstart.prevent="mousedown"
      @mousemove="mousemove"
      @touchmove="mousemove"
      @mouseup="mouseup"
      @mouseleave="mouseup"
      @touchend="mouseup"
      @keydown="keydown($event)"
      @blur="blur($event)"
      @focus="focus($event)"
      @change="view_change"
    />
    <input
      style="display:none"
      type="number"
      :name="name"
      :min="min"
      :max="max"
      :step="step"
      v-model="value"
      @change="change"
    />
  </div>
</template>

<script>
export default {
  props: [
    "name",
    "_class",
    "_min",
    "_max",
    "_precision",
    "_step",
    "_value",
    "_view_multiply",
    "_view_precision",
    "nodrag"
  ],
  mounted() {
    this.blur({
      target: document.querySelector('input[name="' + this.name + '"]')
    });
  },
  data() {
    const data = {
      view_multiply:
        typeof this._view_multiply == "undefined"
          ? 1
          : parseFloat(this._view_multiply),
      view_precision:
        typeof this._view_precision == "undefined"
          ? 2
          : parseInt(this._view_precision),
      value: typeof this._value == "undefined" ? 0 : parseFloat(this._value),
      min: typeof this._min == "undefined" ? -Infinity : parseFloat(this._min),
      max: typeof this._max == "undefined" ? Infinity : parseFloat(this._max),
      precision:
        typeof this._precision == "undefined" ? 2 : parseInt(this._precision),
      drag_step: typeof this._step == "undefined" ? 1 : parseFloat(this._step),
      change_started: false,
      distance: 0,
      prev_view_value: 0,
      pointer: [0, 0],
      prev_pointer: [0, 0]
    };

    data.view_min = this.convert_value_to_value_view(
      data.min,
      data.view_multiply,
      false
    );
    data.view_max = this.convert_value_to_value_view(
      data.max,
      data.view_multiply,
      false
    );

    return data;
  },
  watch: {
    // удалил old_val, new_val из параметров
    _value: function() {
      this.value = this.format_value(parseFloat(this._value));
      this.$emit("input", this.value);
    }
  },
  computed: {
    view_value: function() {
      return this.convert_value_to_value_view(this.value);
    },
    step: function() {
      return Math.pow(10, -this.precision).toFixed(this.precision);
    },
    view_step: function() {
      return Math.pow(10, -this.view_precision).toFixed(this.view_precision);
    },
    inputListeners: function() {
      var vm = this;
      // `Object.assign` объединяет объекты вместе, чтобы получить новый объект
      return Object.assign(
        {},
        // Мы добавляем все слушатели из родителя
        this.$listeners,
        // Затем мы можем добавить собственные слушатели или
        // перезаписать поведение некоторых существующих.
        {
          // Это обеспечит, что будет работать v-model на компоненте
          input: function(event) {
            vm.$emit("input", event.target.value);
          }
        }
      );
    }
  },
  methods: {
    set_value_from_event_target(event) {
      this.value = this.convert_view_value_to_value(event.target.value);
    },
    view_change(event) {
      this.set_value_from_event_target(event);
      this.change(event);
    },
    change() {
      this.$emit("input", this.value);
    },
    keydown(event) {
      if (event.keyCode === 13) {
        event.stopPropagation();
        event.preventDefault();
        this.set_value_from_event_target(event);
        this.$emit("input", this.value);
        this.blur(event);
      }
    },
    blur(event) {
      event.target.style.backgroundColor = "transparent";
      event.target.style.cursor = "col-resize";
    },
    focus(event) {
      event.target.style.backgroundColor = "";
      event.target.style.cursor = "";
    },
    mousedown(event) {
      this.change_started = true;

      event = event.changedTouches ? event.changedTouches[0] : event;
      this.prev_pointer = [event.pageX, event.pageY];
      this.distance = 0;
      this.prev_view_value = this.view_value;

      if (this.nodrag) {
        this.mouseup(event);
      }
    },
    format_view_value(value, clamp = true) {
      let new_value = parseFloat(value).toFixed(this.view_precision);
      if (!clamp) return new_value;
      return Math.min(this.view_max, Math.max(this.view_min, new_value));
    },
    format_value(value) {
      return Math.min(
        this.max,
        Math.max(this.min, parseFloat(value).toFixed(this.precision))
      );
    },
    convert_view_value_to_value(value) {
      return this.format_value(value / this.view_multiply);
    },
    convert_value_to_value_view(value, multiply = undefined, clamp = true) {
      let new_value = value * (multiply || this.view_multiply);
      return this.format_view_value(new_value, clamp);
    },
    mousemove(event) {
      if (!this.change_started) return;

      event = event.changedTouches ? event.changedTouches[0] : event;

      var current_view_value = this.view_value;

      this.pointer = [event.pageX, event.pageY];

      this.distance +=
        this.pointer[0] -
        this.prev_pointer[0] -
        (this.pointer[1] - this.prev_pointer[1]);

      var view_value =
        this.prev_view_value +
        (this.distance / (event.shiftKey ? 5 : 50)) * this.drag_step;

      view_value = this.format_view_value(view_value);

      if (current_view_value !== view_value) {
        this.value = this.convert_view_value_to_value(view_value);
        this.$emit("input", this.value);
      }

      this.prev_pointer = [event.pageX, event.pageY];
    },
    mouseup(event) {
      if (!this.change_started) return;
      this.change_started = false;

      event = event.changedTouches ? event.changedTouches[0] : event;

      if (Math.abs(this.distance) < 2) {
        event.target.focus();
        event.target.select();
      }
    }
  }
};
</script>
