<template>
  <div class="background">
    <div v-show="show" class="main"></div>
  </div>
</template>

<script>
export default {
  name: 'background',
  data() {
    let data = {
      show: false,
    }
    return data;
  },
  mounted() {
    this.$root.$on('bv::dropdown::show', bvEventS => {
      if (bvEventS.componentId == 'menu-editor') {
        document.getElementsByTagName('body')[0].style.overflow = 'hidden'
        document.getElementsByClassName('main')[0].style.height = 'calc(50vh + 46px)'
        document.getElementById(bvEventS.componentId).style.zIndex = 99999999999999;
      } else {
        document.getElementsByTagName('body')[0].style.overflow = 'hidden'
        document.getElementsByClassName('main')[0].style.height = '100vh'
        document.getElementById(bvEventS.componentId).style.zIndex = 99999999999999;
      }
      this.show = true;
    }),

    this.$root.$on('bv::dropdown::hide', bvEventH => {
      if (this.show) {
        document.getElementsByTagName('body')[0].style.overflow = ''
        document.getElementById(bvEventH.componentId).style.zIndex = 0;

        if (bvEventH.componentId == 'menu-editor') {
          document.getElementsByTagName('body')[0].style = 'width: 100%; height: 100%; overflow: hidden;'
        }
      }
      this.show = false;
    })
  }, 
}
</script>

<style scoped>
.background {}

.main {
  background-color: #000000a1;
  position: absolute;
  z-index: 99;
  top: 64px;
  left: 0;
  width: 100vw;
  height: 100vh;
}
</style>