<template>
    <div class="popup-load-images">
      <div class="content p-3">
        <div class="header">
          <h5><b>Add an Image</b></h5>
          <b><span @click="close_" class="close">&times;</span></b>
        </div>
        
        <div class="text-info">
          <p>Upload a file from your device</p>
        </div>

        <div class="block-button mt-3">
          <input type="file" id="input-file" style="display: none;">
          <button type="button" class="button-file button-choise" @click="choiseFile">Choose a file…</button>
          <button type="button" class="button-file button-my-file">
            <img style="width: 24%;" src="../../assets/img/Folder.png" alt="">
            My Files
          </button>
        </div>

        <div class="block-data mt-3">
          <span>Supported format: <b>{{data_format}}</b></span>
          <span>Max file size: <b>{{data_size}} MB</b></span>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'load-images',
  props: ['close'],
  data() {
    let main_elem = document.getElementsByTagName('body')[0]
    let data = {
      main_elem,
      data_format: 'JPG, JPEG, PNG, GIF',
      data_size: '50',
    }
    return data;
  },
  mounted() {
    window.scrollTo( 0, 0 );
  },
  methods: {
    close_() {
      document.getElementsByTagName('body')[0].style = '';
      this.close()
    },
    choiseFile() {
      document.getElementById('input-file').click()
    }
  }
}
</script>

