<template>
  <div class>
    <input
      type="file"
      style="display:none"
      :id="file_id"
      :accept="file_accept"
      @change="upload_files"
    />
    <div
      class
      style="
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      "
    >
      <span class="inp-block" :id="status_id" v-show="show_status" disabled>{{ status }}</span>
    </div>

    <div
      v-if="addUrl"
      style="display: flex;justify-content: center; width: 100%;"
      class="button-block"
    >
      <button
        v-if="!url_added_file"
        class="button-choose"
        @click.prevent="select_files"
      >Choose file...</button>
      <button
        v-if="url_added_file"
        class="button-choose-change"
        @click.prevent="select_files"
      >Change model</button>

      <div v-if="url_added_file" style="width: 50%">
        <button
          v-if="url_added_file"
          class="button-applay"
          style="align-items: center;justify-content: center;display: flex;"
          @click="getUrl(url_added_file, name_added_file)"
        >Use this model</button>
        <button
          v-else
          class="button-applay"
          style="align-items: center;justify-content: center;display: flex;"
          @click="getUrl(value)"
        >Use this model</button>
      </div>
    </div>
    <div v-else style="display: flex;justify-content: center;" class="button-block">
      <button class="button-choose" @click.prevent="select_files">Choose file...</button>
    </div>
  </div>
</template>

<script>
import { transliterate } from "transliteration";
import Helper from "../../Scripts/modules/Helper.js";

export default {
  props: [
    "name",
    "_value",
    "put_url" /* /account/storage/put/ObjectList/2/url/ */,
    "public_url" /* https://mywebar-a.akamaihd.net/ */,
    "public_user_url" /* https://mywebar-a.akamaihd.net/1/ */,
    "allow_upload",
    "rules",
    "editor",
    "getUrl",
    "addUrl",
    "url_added_file",
    "name_added_file"
  ],
  created() {},
  mounted() {
    this.file_el = document.getElementById(this.file_id);
    this.status_el = document.getElementById(this.status_id);
    this.url_el = document.getElementById(this.url_id);
  },
  data() {
    const id_prefix = this.name.replace(/[^a-z\d]/gi, "_");
    const allow_upload_ext = this.allow_upload.ext || [];
    const data = {
      status: "Ok.",
      show_status: false,
      value: typeof this._value == "undefined" ? "" : this._value,
      file_id: id_prefix + "_file",
      status_id: id_prefix + "_status",
      url_id: id_prefix + "_url",
      allow_upload_ext,
      file_accept: allow_upload_ext.length
        ? allow_upload_ext.map(ext => `.${ext}`).join(", ")
        : "*/*",
      url_callback: this.addUrl ? this.addUrl : this.getUrl
    };
    return data;
  },
  computed: {
    inputListeners: function() {
      var vm = this;
      // `Object.assign` объединяет объекты вместе, чтобы получить новый объект
      return Object.assign(
        {},
        // Мы добавляем все слушатели из родителя
        this.$listeners,
        // Затем мы можем добавить собственные слушатели или
        // перезаписать поведение некоторых существующих.
        {
          // Это обеспечит, что будет работать v-model на компоненте
          input: function(event) {
            vm.$emit("input", event.target.value);
          }
        }
      );
    }
  },
  methods: {
    async upload_files() {
      const files = Array.from(this.file_el.files);

      for (const file of files) {
        const old_url = this.value;

        const show_error = async message => {
          this.show_status = true;
          this.status = message;
          this.status_el.style.borderColor = "red";
          await new Promise(rv =>
            setTimeout(() => {
              this.status_el.style.borderColor = "";
              this.value = old_url;
              this.show_status = false;
              rv();
            }, 5000)
          );
        };

        if (
          this.allow_upload.maxSize &&
          file.size > this.allow_upload.maxSize
        ) {
          show_error("Too big.");
          continue;
        }

        const show_progress = message => {
          this.show_status = true;
          this.status = message;
        };

        const file_uri =
          this.editor.page.id +
          "/" +
          encodeURIComponent(
            transliterate(file.name).replace(/[^a-zA-Z0-9.,-]/g, "_")
          );

        show_progress("Sending " + file_uri);

        const buffer = await file.arrayBuffer();
        console.log("read buffer length", buffer.byteLength, "from", file_uri);

        const input_url = file_uri => {
          if (!/^https?:/.test(file_uri))
            file_uri = this.public_user_url + file_uri;
          this.show_status = false;
          this.value = file_uri;
          this.$emit("input", this.value);
        };

        try {
          const cdn_url = await Helper.storagePut(
            this.put_url,
            file_uri,
            buffer
          );

          console.log(cdn_url)

          const new_cdn_url = await Helper.storageConvert(
            this.allow_upload,
            file_uri,
            new_url => {
              this.value = new_url;
            },
            show_progress
          );

          input_url(new_cdn_url);
          this.url_callback(this.value, file_uri);
        } catch (e) {
          await show_error(e);
        }

        // empty 'files' prop
        this.file_el.value = "";
      }
    },
    select_files() {
      this.file_el.click();
    },
    change() {
      this.$emit("input", this.value);
    },
    keydown(event) {
      if (event.keyCode === 13) {
        event.stopPropagation();
        event.preventDefault();
        this.$emit("input", this.value);
      }
    }
  }
};
</script>

