<script>
import Helper from "../../Scripts/modules/Helper.js";

export default {
  props: [
    "editor",
    "type",
    "name",
    "object_list",
    "behavoiur_list",
    "var_name",
    "flag_show",
    "toggle_callback",
    "get_url_callback",
    "_put_params",
    "_allow_upload",
    "_rules",
    "_value"
  ],

  data() {
    const data = {
      flag_show_internal: this.flag_show,
      setupData(props) {
        if (props.object_list) {
          this.put_params = {
            type: "ObjectList",
            id: props.object_list.id,
            var_name: props.var_name
          };
          this.allow_upload = props.object_list.allow_upload[props.var_name];
          this.rules = props.object_list.rules[props.var_name];
        } else if (props.behavoiur_list) {
          this.put_params = {
            type: "BehaviourList",
            id: props.behaviour_list.id,
            var_name: props.var_name
          };
          this.allow_upload = props.behaviour_list.allow_upload[props.var_name];
          this.rules = props.behaviour_list.rules[props.var_name];
        } else {
          this.put_params = props._put_params;
          this.allow_upload = props._allow_upload;
          this.rules = props._rules;
        }
      }
    };
    data.setupData(this);
    return data;
  },

  watch: {
    flag_show(new_value) {
      this.flag_show_internal = new_value;
    }
  },

  computed: {
    storage_info() {
      return this.editor
        ? this.editor.storage_info
        : Helper.storageInfo();
    }
  },

  // computed: {
  //   async storage_info() {
  //     return this.editor
  //       ? this.editor.storage_info
  //       : await Helper.storageInfo();
  //   }
  // },

  methods: {
    toggle() {
      (this.toggle_callback ?? this.toggle_callback_internal)();
    },
    toggle_callback_internal() {
      this.flag_show_internal = !this.flag_show_internal;
    }
  }
};
</script>
