import { GLTFLoader } from "../devar/webar/vendor/three/3rdparty/jsm/loaders/GLTFLoader.js";
import { FBXLoader } from "../devar/webar/vendor/three/3rdparty/jsm/loaders/FBXLoader.js";
import { ColladaLoader } from "../devar/webar/vendor/three/3rdparty/jsm/loaders/ColladaLoader.js";

import { MTLLoader } from "../devar/webar/vendor/three/3rdparty/jsm/loaders/MTLLoader.js";
import { OBJLoader2 } from "../devar/webar/vendor/three/3rdparty/jsm/loaders/OBJLoader2.js";
import { MtlObjBridge } from "../devar/webar/vendor/three/3rdparty/jsm/loaders/obj2/bridge/MtlObjBridge.js";

import axios from 'axios'
/*
 * Loaders
 */

const VideoLoader = class {
    constructor() {
        this.loadMetaTimeout = false;
        this.inited = false;
        this.video = document.createElement("video");
    }
    load(url) {
        return new Promise((resolve, reject) =>
            Object.assign(this.video, {
                src: url,
                controls: false,
                autoplay: true,
                muted: true,
                loop: true,
                playsInline: true,
                crossOrigin: "anonymous",
                onended: () => {
                    // rewind
                    this.video.currentTime = 0;
                    if (this.video.loop) this.video.play();
                },
                onerror: e => {
                    //console.log("video error", e);
                    reject(e);
                },
                oncanplay: () => {
                    //console.log("can play video! ", this.video);
                    // have enough data to play
                    this.video.play();
                },
                onloadedmetadata: () => {
                    //console.log("video loadedmetadata ", ...args);
                    clearTimeout(this.loadMetaTimeout);
                    if (!this.video.autoplay) this.video.play(); // if autoplay eq false
                },
                onloadstart: () => {
                    //console.log("video loadstart ", ...args);
                    this.loadMetaTimeout = setTimeout(
                        () => this.video.onerror("load metadata timeout"),
                        60 * 1000
                    );
                },
                onplaying: () => {
                    /*console.log(
                        `playing video ${this.video.videoWidth}x${this.video.videoHeight}! `,
                        this.video
                    );*/

                    if (this.inited) {
                        return;
                    }
                    this.inited = true;

                    //console.log("resolve video");
                    resolve(this.video);
                }
            })
        );
    }
};

class ModelLoader {
    constructor() {
        //
    }
    load(url, ext, onprogress = null) {
        return new Promise((resolve, reject) => {
            let model_name;
            const m = url.match(/\/([^/]+)\.([^.?]+)(\?.*)?$/);
            if (m) {
                if (typeof ext !== "string") {
                    ext = m[2].toLowerCase();
                }
                model_name = m[1];
            }
            let loader,
                promise = new Promise(rv => rv()),
                onload = model => {
                    console.log("loaded model ", url, model);
                    resolve({
                        mesh: model.scene,
                        animations: model.animations ?? []
                    });
                };
            switch (ext) {
                case "gltf":
                case "glb": {
                    loader = new GLTFLoader();
                    break;
                } case "fbx": {
                    loader = new FBXLoader();
                    onload = model => {
                        resolve({
                            mesh: model,
                            animations: model.animations ?? []
                        });
                    };
                    break;
                } case "dae": {
                    loader = new ColladaLoader();
                    break;
                } case "mtl": {
                    loader = new MTLLoader();
                    onload = mtl => {
                        resolve(mtl);
                    };
                    break;
                } case "obj": {
                    loader = new OBJLoader2();
                    const mtl_url = new URL(url);
                    mtl_url.pathname = mtl_url.pathname.replace(
                        new RegExp("." + ext + "$"),
                        ".mtl"
                    );
                    promise = new ModelLoader()
                        .load(mtl_url.href)
                        .catch(error => console.warn("mtl load error", error));
                    promise = promise.then(mtl_result => {
                        if (!mtl_result) return;
                        loader.setModelName(model_name);
                        loader.setLogging(true, true);
                        loader.addMaterials(
                            MtlObjBridge.addMaterialsFromMtlLoader(mtl_result),
                            true
                        );
                    });
                    onload = model => {
                        resolve({
                            mesh: model,
                            animations: model.animations ?? []
                        });
                    };
                    break;
                } default: {
                    reject("unsupported extension " + JSON.stringify(ext));
                }
            }

            promise.then(() =>
                loader.load(
                    url,
                    onload,
                    // called while loading is progressing
                    onprogress /*function(xhr) {
                        console.log(
                            (xhr.loaded / xhr.total) * 100 + "% loaded"
                        );
                    }*/,
                    // called when loading has errors
                    function(error) {
                        reject(error);
                    },
                    null
                )
            );
        });
    }
}

const formUrlEncode = (form, submit_name) => {
    return Array.from(form.elements)
        .reduce((result, el) => {
            if (!el.checkValidity()) {
                el.reportValidity();
                throw { type: "validity", el };
            }
            if (el.type == "radio" && !el.checked) return result;
            if (el.type == "checkbox" && !el.checked) return result;
            if (el.type == "button") return result;
            if (submit_name && el.type == "submit" && el.name != submit_name)
                return result;
            if (el.name)
                result.push(
                    encodeURIComponent(el.name)
                        .replace(/%5B/gi, "[")
                        .replace(/%5D/gi, "]") +
                        "=" +
                        encodeURIComponent(el.value)
                );
            return result;
        }, [])
        .join("&");
};

/*
 * Forms
 */

const axiosFormSubmitData = (form, formdata) => {
    return axios[form.method](form.action, formdata);
};

const axiosFormSubmit = (form, submit_name) => {
    const formdata = formUrlEncode(form, submit_name);
    return axiosFormSubmitData(form, formdata);
};

const axiosForm = (form, submit_name) => {
    return () => axiosFormSubmit(form, submit_name);
};

/*
 * Storage
 */

let storage_info;
let storage_namespace = "/account/";

const storageNamespace = namespace => {
    storage_namespace = "/" + encodeURIComponent(namespace) + "/";
};

const storageInfo = async refresh => {
    if (!refresh && storage_info) return storage_info;
    storage_info = (await axios.get(storage_namespace + "storage/info")).data;
    return storage_info;
};

const makeFileListFilter = allow_upload => {
    const ext = allow_upload.ideal ? [allow_upload.ideal] : allow_upload.ext;
    const regex = new RegExp("\\.(" + ext.join("|") + ")$", "i");
    return function(file) {
        return (
            file.name &&
            regex.test(file.name) &&
            !/.__preview__\.[^.]+$/.test(file.name)
        );
    };
};

const storageFilterList = async (url, filter_cb) => {
    if (!storage_info) storage_info = await storageInfo();
    let files = (await axios.get(url)).data;
    const previews = new Map();
    files.filter(file => {
        const parent_filename = fileNameIsPreviewFor(file.name);
        if (typeof parent_filename === "string") {
            previews.set(parent_filename, file.name);
            return false;
        }
        return true;
    });
    if (filter_cb) files = files.filter(filter_cb);
    files = files.map(
        fileAssignAdditionalCallback(storage_info.container_url, previews)
    );
    return files;
};

const fileAssignAdditionalCallback = (url_prefix, previews) => {
    return file => {
        if (previews.has(file.name)) {
            file.preview_url = url_prefix + previews.get(file.name);
        }
        file.url = url_prefix + file.name;
        file.filename = file.name.split(/\//g).pop();
        file.printname = file.filename
            .replace(/[_\s]+/g, " ")
            .replace(/.([^.]+)$/, "")
            .trim();
        if (file.printname === "") file.printname = file.filename;
        return file;
    };
};

const storageListFilter = async (filter_cb, dir = "") => {
    if (dir.length && dir[0] != "/") dir = "/" + dir;
    const files = await storageFilterList(
        storage_namespace + "storage/list" + dir,
        filter_cb
    );
    return files;
};

const storageListExamplesFilter = async (filter_cb, dir = "") => {
    if (dir.length && dir[0] != "/") dir = "/" + dir;
    const files = await storageFilterList(
        storage_namespace + "storage/list_examples" + dir,
        filter_cb
    );
    return files;
};

const storageList = async (allow_upload, dir) => {
    return await storageListFilter(
        allow_upload ? makeFileListFilter(allow_upload) : undefined,
        dir
    );
};

const storageListExamples = async (allow_upload, dir) => {
    return await storageListExamplesFilter(
        allow_upload ? makeFileListFilter(allow_upload) : undefined,
        dir
    );
};

const storagePut = async (put_params, file_uri, body) => {
    let put_url = put_params;
    if (typeof put_params == "object") {
        const { type, id, var_name = "url" } = put_params;
        put_url = `${storage_namespace}storage/put/${type}/${id}/${var_name}/`;
    } else if (
        typeof put_url == "string" &&
        put_url.indexOf(storage_namespace) !== 0
    ) {
        put_url = storage_namespace + put_url;
    } else {
        put_url = `${storage_namespace}storage/put/`;
    }
    if (!storage_info) storage_info = await storageInfo();
    const put_result = await axios.post(put_url + file_uri, body);
    if (put_result.status === 201) {
        return storage_info.user_url + file_uri;
    } else {
        throw "HTTP " + put_result.status;
    }
};

const fileNameIsPreviewFor = filename => {
    const m = filename.match(/^(.*).__preview__\.[^.]+$/);
    return m ? m[1] : undefined;
};

const storagePutPreview = async (parent_file_uri, body) => {
    const put_params =
        storage_namespace == "/account/"
            ? { type: "ObjectList", id: 1, var_name: "url" }
            : undefined;
    return await storagePut(
        put_params,
        parent_file_uri + ".__preview__.jpg",
        body
    );
};

const storageConvert = async (
    allow_upload,
    file_uri,
    on_new_url,
    on_progress,
    check_interval = 2000
) => {
    const ideal_ext = allow_upload.ideal,
        ext = file_uri
            .split(".")
            .pop()
            .toLowerCase();

    if (ideal_ext && ideal_ext !== ext) {
        let convert_result;
        do {
            convert_result = await axios.get(
                storage_namespace +
                    "storage/convert/" +
                    file_uri +
                    "?ext=" +
                    ideal_ext
            );

            if (convert_result.data.new_url && on_new_url) {
                on_new_url(convert_result.data.new_url);
            }

            if (on_progress) on_progress(convert_result.data.progress);

            await new Promise(rv => setTimeout(rv, check_interval));
        } while (!convert_result.data.done);

        if (convert_result.data.new_url) {
            return convert_result.data.new_url;
        } else {
            throw convert_result.data.progress;
        }
    } else {
        if (!storage_info) storage_info = await storageInfo();
        return storage_info.user_url + file_uri;
    }
};

const storageDelete = async file_uri => {
    if (typeof file_uri === "string") {
        try {
            const delete_result = await axios.post(
                storage_namespace + "storage/delete/" + file_uri
            );
            if (delete_result.status === 204) {
                return true;
            }
        } catch (e) {
            console.warn("delete error", e);
        }
    }
    return false;
};

const storageDeleteUrl = async url => {
    if (!storage_info) storage_info = await storageInfo();
    const file_uri = CdnUrlToFileUri(url);
    if (!file_uri) return;
    return await storageDelete(file_uri);
};

const CdnUrlToFileUri = cdn_url => {
    if (!storage_info) throw "call storageInfo() first";
    const file_uri = cdn_url.replace(storage_info.user_url, "");
    if (cdn_url === file_uri) {
        return;
    }
    return file_uri;
};

export default {
    VideoLoader,
    ModelLoader,
    formUrlEncode,
    axiosForm,
    axiosFormSubmit,
    axiosFormSubmitData,

    storageNamespace,
    storageInfo,
    makeFileListFilter,
    CdnUrlToFileUri,
    storageListFilter,
    storageListExamplesFilter,
    storageList,
    storageListExamples,
    storagePut,
    storagePutPreview,
    storageConvert,
    storageDelete,
    storageDeleteUrl
};
