<template>
    <div :class="[{fix_position: fixed}, 'header']">
        <div class="wrapper-content m-2">
            <router-link to='/home'>
              <img class="img-width-logo" src='../assets/img/Header-logo.png' alt="Logo">
            </router-link>

            <b-dropdown 
                id="dropdown-header" 
                toggle-class="text-decoration-none bg-transparent border-0 m-0 p-0"
                menu-class="border-0"
                button-class='p-0'
                size="lg"
                offset="0,10"
                right
                block
            >
                <template v-slot:button-content>
                  <img class="img-width-logo-user" src='../assets/img/Logo-user.png' alt="Logo">
                </template>

                <div class="block-user-data">
                    <span class="user-name">{{name}}</span>
                    <span class="user-email">{{email}}</span>
                </div>

                <b-dropdown-divider></b-dropdown-divider>
                
                <b-dropdown-item><router-link to='/home'>Dashboard</router-link></b-dropdown-item>
                <b-dropdown-item><router-link to='/profile'>Account</router-link></b-dropdown-item>
                <b-dropdown-item @click="helpShow"><router-link to='#'>Support</router-link></b-dropdown-item>
                <b-dropdown-item>Plans</b-dropdown-item>
                <b-dropdown-item>Log Out</b-dropdown-item>
            </b-dropdown>
        </div>
        <popup-help
          v-if="help"
          :close="helpShow"
        ></popup-help>

        <background></background>
    </div>
</template>

<script>
import VueCookies from 'vue-cookies'
import axios from '../Scripts/service/axios.js';

export default {
  name: 'header-page',
  props: ["fixed"],
  data() {
    let data = {
      name: 'John Marston',
      email: 'john@mail.com',
      help: false,
    }
    return data
  },
  mounted() {    
    if (!VueCookies.get('AuthToken')) {
      this.$router.push('/')
    }

    axios.post('details/').then(res => {
      this.email = res.data.success.email;
      this.name = res.data.success.name;
    })
    .catch(err => {
      console.error(err)
      this.$router.push('/')
    })
  },
  methods: {
    helpShow() {
      this.help = !this.help;
      document.getElementsByTagName('body')[0].style.overflow = this.help?'hidden':''
    }
  }
}
</script>

