<script>
import * as THREE from "../../Scripts/devar/webar/vendor/three/build/three.module.js";

import { GLTFLoader } from "../../Scripts/devar/webar/vendor/three/3rdparty/jsm/loaders/GLTFLoader.js";
import { OrbitControls } from "../../Scripts/devar/webar/vendor/three/3rdparty/jsm/controls/OrbitControls.js";
import { TransformControls } from "../../Scripts/devar/webar/vendor/three/3rdparty/jsm/controls/TransformControls.js";
Object.assign(THREE, { GLTFLoader, OrbitControls, TransformControls });

import * as ObjectListLoad from "../../Scripts/objects/ObjectList.js";

import Helper from "../../Scripts/modules/Helper.js";

import Vue from 'vue'

import EditorPageObjectParams3 from './EditorPageObjectParams';

export default {
  props: [
    "prop_object_lists",
    "prop_behaviour_lists",
    "objects_target_id",
    "negative_id_couner",
    "scene_id",
    "prop_storage_info",
    "prop_ar_modes",
    "prop_routes",
    "prop_page",
    "limits",
    "user"
  ],
  created() {
    // this.$root.d.editor = this;
  },
  mounted() {
    console.log(this._transform_scale_x, 'this._transform_scale_x')
    // Закрытие попап окон
    document.addEventListener("click", e => {
      if (e.toElement.className == "popup-qr") {
        this.showQrPopup();
      }
    });

    document.addEventListener("click", e => {
      if (e.toElement.className == "popup-customization") {
        this.showCustomPopup();
      }
    });

    document.addEventListener("click", e => {
      if (e.toElement.className == "popup-help_") {
        this.toggleHelpPopup();
      }
    });

    this.scene.rotation.fromArray(this.rotations);

    const container = document.getElementById(this.scene_id);
    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.outputEncoding = THREE.sRGBEncoding;
    this.renderer.shadowMap.enabled = true;
    container.appendChild(this.renderer.domElement);

    /*
	  // QR on scene canvas bg
    Object.assign(this.renderer.domElement.style, {
      backgroundRepeat: "no-repeat",
      backgroundPosition: "center",
      backgroundSize: "auto 24.15%",
      backgroundImage: "url(" + this.qrcode_image_url + ")"
    });
    */

    // cors Error  
    this.updateMarkerPlane();

    /*
    // background
    new THREE.TextureLoader().load(
      "https://mywebar.com/data/phone_shop.jpg",
      texture => {
        texture.mapping = THREE.UVMapping;
        this.scene.background = new THREE.WebGLCubeRenderTarget(1024, {
          generateMipmaps: true,
          minFilter: THREE.LinearMipmapLinearFilter,
          magFilter: THREE.LinearFilter
        }).fromEquirectangularTexture(this.renderer, texture);
      }
    );
    */

    // grid helper
    const gridhelper = new THREE.GridHelper(20, 100, 0x222222, 0x444444);
    gridhelper.rotation.x = Math.PI / 1;
    gridhelper.position.z = -0.001;
    gridhelper.position.y = -0.001;
    this.scene.add(gridhelper);
    this.renderer.setClearColor(0xe0e0e0, 1);

    /*
    this.renderer_css.domElement.style.position = "absolute";
    this.renderer_css.domElement.style.top = 0;
    this.renderer_css.domElement.style.left = 0;
    container.appendChild(this.renderer_css.domElement);
    */

    this.camera.position.set(0, 5, 5); //.set(0, 5, 0);//
    this.camera.lookAt(this.scene.position);

    /*
    // https://andi-siess.de/rgb-to-color-temperature/
    const ambientlight = new THREE.AmbientLight(0xffa957); // 255, 169, 87
    this.scene.add(ambientlight);

    const pointLight = new THREE.PointLight(0xff80c0, 2, 0);
    pointLight.position.copy(this.camera.position);
    this.scene.add(pointLight);
	  */

    // https://andi-siess.de/rgb-to-color-temperature/    
    const ambientlight = new THREE.AmbientLight(0xffffff, 3); // 255, 169, 87
    this.scene.add(ambientlight);

    const pointLight = new THREE.DirectionalLight(0xffffff, 0.5);
    pointLight.position.set(10, 10, 0);
    this.scene.add(pointLight);

    // orbit controls    
    this.orbitControls = new OrbitControls(
      this.camera,
      this.renderer.domElement
    );

    // /orbit controls
    // transform controls
    this.transformControls = new TransformControls(
      this.camera,
      this.renderer.domElement
    );

    const $this = this;

    this.transformControls.addEventListener("objectChange", () => {
      // change
      const ctl = $this.transformControls,
        pos = ctl.object.position,
        rot = ctl.object.rotation,
        scale = ctl.object.scale,
        params = $this.page_object_params.get(ctl.object.name);
      [
        params.transform_pos_x,
        params.transform_pos_y,
        params.transform_pos_z
      ] = pos.toArray();
      pos.fromArray([
        params.transform_pos_x,
        params.transform_pos_y,
        params.transform_pos_z
      ]);

      [
        params.transform_rot_x,
        params.transform_rot_y,
        params.transform_rot_z
      ] = rot.toArray();
      rot.fromArray([
        params.transform_rot_x,
        params.transform_rot_y,
        params.transform_rot_z
      ]);

      [
        params.transform_scale_x,
        params.transform_scale_y,
        params.transform_scale_z
      ] = scale.toArray();
      scale.fromArray([
        params.transform_scale_x,
        params.transform_scale_y,
        params.transform_scale_z
      ]);
    });

    this.transformControls.addEventListener("dragging-changed", event => {
      $this.orbitControls.enabled = !event.value;
    });

    const radio = this.form.elements.transform_mode,
      set_mode = mode => {
        console.log("set transform mode ", mode);
        $this.transformControls.setMode(mode);
      }
      ,
      update_mode = event => {
        console.log("click input", event);
        radio.value = event.target.value;
        set_mode(radio.value);
      };
  
    Array.from(document.getElementById('transform_mode').querySelectorAll(".btn")).forEach(btn => {
      console.log("set onclick on ", btn);
      btn.onclick = update_mode;
    });

    set_mode(radio.value);

    this.scene.add(this.transformControls);

    // /transform controls
    this.deb_onWindowResize();
    window.addEventListener("resize", this.deb_onWindowResize, false);

    this.page_objects.forEach(object => this.add(object));

    setTimeout(() => {
      this.last_saved_formdata = Helper.formUrlEncode(this.form);
    }, 10);

    window.onbeforeunload = e => {
      if (this.ignore_changes) return;
      const formdata = this.get_changed_formdata();
      if (formdata) {
        const dialogText = "Form on this page have unsaved changes.";
        e.returnValue = dialogText;
        return dialogText;
      }
    };

    // window.Helper = Helper;
  
    this.animate();

    document.getElementsByTagName('body')[0].style = ''
  },
  computed: {
    form() {
      return this.$el;
    },
    // qr code url
    page_image_url() {
      return this.page.image_url;
    },
    // scene preview image url
    page_preview_image_url() {
      return this.page.preview_image_url;
    },
    // nft image url
    page_marker_image_url() {
      return this.page.marker_image_url;
    },
    // image url for marker plane texture
    marker_plane_texture_image_url() {
      let url = null;
      switch (this.page.ar_mode.name) {
        case "qr":
          url = this.page_image_url;
          break;
        case "image":
          url = this.page_marker_image_url;
          break;
      }
      return url;
    },
    page_preview_state_logo() {
      try {
        return this.page.marker_state.logo;
      } catch {
        return "";
      }
    },
    page_preview_state_color1() {
      try {
        return this.page.marker_state.color1;
      } catch {
        return "#000000";
      }
    },
    page_preview_state_color2() {
      try {
        return this.page.marker_state.color2;
      } catch {
        return "#fff";
      }
    },
    page_preview_state_radius() {
      try {
        return this.page.marker_state.logoRadius;
      } catch {
        return 0;
      }
    }
  },
  data() {
    const behaviour_lists = [...this.prop_behaviour_lists];
    const object_lists = [...this.prop_object_lists];
    const ar_modes = [...this.prop_ar_modes];
    const routes = { ...this.prop_routes };
    const page = { ...this.prop_page };
    const storage_info = { ...this.prop_storage_info };

    const page_objects = page.objects;

    return {
      ar_modes,
      routes,
      page,
      object_lists,
      behaviour_lists,
      page_objects,
      storage_info,
      page_object_params: new Map(),
      editor: this,
      scene: new THREE.Scene(),
      camera: new THREE.PerspectiveCamera(
        45,
        window.innerWidth / window.innerHeight,
        0.01,
        100
      ),
      renderer: new THREE.WebGLRenderer({ antialias: true, alpha: true }),
      //renderer_css: new CSS3DRenderer(),
      clock: new THREE.Clock(),
      raycaster: new THREE.Raycaster(),
      // deb_onWindowResize: _.debounce(this.onWindowResize, 500),
      deb_onWindowResize: this.onWindowResize,
      rotations: [0, 0, 0], //[-Math.PI / 2, 0, 0],
      orbitControls: undefined,
      transformControls: undefined,
      last_saved_formdata: undefined,
      ignore_changes: false,

      data_id: undefined,
      object_list: undefined,
      list_id: [],
      show_qr_popup: false,
      data_name: undefined,
      show_custom_popup: false,
      marker_plane: undefined,
      THREE,

      show_popup_marker: false,
      show_help_popup: false,
      show_status_popup: false,
      text_status: '',
      type_status: '',
      elements_controls: false,

      selected_project: undefined,
      added_projects: [],
      flag_popup_1: false,
      flag_popup_2: false,
      flag_popup_3: false,
      flag_popup_4: false,
      status_popup: false,
      text_message: '',
      type_message: '',
    };
  },
  methods: {
    closePopup1() {
      this.flag_popup_1 = !this.flag_popup_1;
    },
    closePopup2() {
      this.flag_popup_2 = !this.flag_popup_2;
    },
    closePopup3() {
      this.flag_popup_3 = !this.flag_popup_3;
    },
    closePopup4() {
      this.flag_popup_4 = !this.flag_popup_4;
    },
    closeStatus() {
      this.status_popup = !this.status_popup;
    },
    selectProject(id) {
      this.selected_project = id;

      this.added_projects.forEach(item => {
        if (item.id == id) {
          document.getElementById('select-prj'+item.id).style.opacity = 1;
          document.getElementById('object_'+item.id).style.display = 'block';
        } else {
          document.getElementById('select-prj'+item.id).style.opacity = 0.6;
          document.getElementById('object_'+item.id).style.display = 'none';
        }
      })

      this.elements_controls = true;
    },
    showPopupStatus() {
      this.show_status_popup = !this.show_status_popup;
    },
    updateMarkerPlane() {
      const texture_url = this.marker_plane_texture_image_url;

      if (this.marker_plane) {
        this.scene.remove(this.marker_plane);
        this.marker_plane = undefined;
      }

      if (!texture_url) {
        return;
      }

      const marker_texture = new THREE.TextureLoader().load(
        texture_url,
        texture => {
          const texture_ratio = texture.image.width / texture.image.height;

          if (texture_ratio > 1) {
            this.marker_plane.scale.set(1, 1 / texture_ratio, 1);
          } else {
            this.marker_plane.scale.set(texture_ratio, 1, 1);
          }

          texture.needsUpdate = true;
        }
      );

      this.marker_plane = new THREE.Mesh(
        new THREE.PlaneGeometry(1, 1),
        new THREE.MeshBasicMaterial({
          map: marker_texture,
          side: THREE.DoubleSide
        })
      );

      this.marker_plane.rotation.x = -Math.PI / 2;

      this.scene.add(this.marker_plane);
    },
    showQrPopup() {
      this.show_qr_popup = !this.show_qr_popup;
    },
    showCustomPopup() {
      this.show_custom_popup = !this.show_custom_popup;
    },
    toggleHelpPopup() {
      this.show_help_popup = !this.show_help_popup;
    },

    ele(name, id) {
      this.data_id = id;
      this.data_name = name;

      this.elements_controls = !this.elements_controls
    },

    screenshot_and_submit(event) {
      const width = 512,
        height = width;

      let obj_screen = new THREE.WebGLRenderer({
        preserveDrawingBuffer: true,
        antialias: true
      });

      obj_screen.setSize(width, height, false);
      obj_screen.setClearColor(0xe0e0e0, 1);
      obj_screen.setPixelRatio(window.devicePixelRatio);
      obj_screen.outputEncoding = THREE.sRGBEncoding;
      obj_screen.shadowMap.enabled = true;

      this.camera.aspect = width / height; // / this.camera.aspect
      this.camera.updateProjectionMatrix();

      obj_screen.render(this.scene, this.camera);

      setTimeout(() => {
        obj_screen.domElement.toBlob(
          blob => {
            console.log("screenshot: ", URL.createObjectURL(blob));
            this.onWindowResize(); // to recalculate camera aspect
            this.send_screenshot(blob).then(() => {
              this.submit(event);
            });
          },
          "image/jpeg",
          0.8
        );
      }, 1);
    },

    async send_screenshot(blob) {
      this.page.preview_image_url =
        (await Helper.storagePutPreview("page_" + this.page.id, blob)) +
        "?_=" +
        Date.now();
      console.log(this.page.preview_image_url, "save preview");
      return this.page.preview_image_url;
    },

    get_changed_formdata() {
      const formdata = Helper.formUrlEncode(this.form);
      if (formdata != this.last_saved_formdata) return formdata;
    },
    submit(event, ondone) {
      const formdata = this.get_changed_formdata();
      if (formdata) {
        if (event) event.preventDefault();
        Helper.axiosFormSubmitData(this.form, formdata)
          .then(() => {
            this.last_saved_formdata = formdata;
            if (typeof ondone == "function") {
              ondone();
            } else {
              this.text_status = "All changes saved successfully."
              this.type_status = 'sucsess'
              this.showPopupStatus()
            }
          })
          .catch(error => {
            // error.response.data
            const data_error =
              error.response && error.response.data
                ? error.response.data
                : null;
            if (data_error) {
              this.text_status = "save data errors: " + JSON.stringify(data_error, null, "\t");
              this.type_status = 'error';
              this.showPopupStatus()
            } else {
              this.text_status = "save network error: " + error;
              this.type_status = 'error';
              this.showPopupStatus()
            }
          });
      } else if (typeof ondone == "function") {
        ondone();
      }
    },
    normalize_size(object3d) {
      console.warn("normalize size of ", object3d);
      let size3 = object3d.size3;
      if (!size3) {
        size3 = new THREE.Vector3();
        new THREE.Box3().setFromObject(object3d).getSize(size3);
      }
      const scale = 1 / Math.max.apply(this, size3.toArray());
      object3d.scale.setScalar(scale);
    },
    animate() {
      this.page_object_params.forEach(o => {
        if (
          typeof o.object == "object" &&
          typeof o.object.animate == "function"
        )
          o.object.animate();
      });
      this.renderer.render(this.scene, this.camera);
      //this.renderer_css.render(this.scene, this.camera);
      requestAnimationFrame(this.animate);
    },
    onWindowResize() {
      const container = document.getElementById(this.scene_id);
      this.camera.aspect = container.clientWidth / container.clientHeight;
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(container.clientWidth, container.clientHeight);
      //this.renderer_css.setSize(container.clientWidth, container.clientHeight);
    },
    remove(data_id) {
      this.data_id = "";
      this.data_name = "";

      if (this.page_object_params.has(data_id)) {
        const params = this.page_object_params.get(data_id);
        if (params.object3d == this.transformControls.object) {
          console.log("datach transform controls from ", params.object3d);
          this.transformControls.detach();
          this.elements_controls = !this.elements_controls;
        } else {
          console.log(
            " transform controls not datached from",
            this.transformControls.object
          );
        }
        this.scene.remove(params.object3d);
        this.scene.remove(params.helper);
        this.page_object_params.delete(data_id);
        window["object_" + data_id].remove();
      }
    },
    load(page_object, object_list) {
      page_object.indicator = new THREE.Mesh(
        new THREE.SphereBufferGeometry(0.5, 64, 64),
        new THREE.MeshBasicMaterial({
          color: 0x00ff00,
          opacity: 0.4,
          transparent: true,
          alphaTest: 0.05
        })
      );
      page_object.indicator.onerror = () => {
        page_object.indicator.material.color.setHex(0xff0000);
      };

      if (typeof object_list != "object")
        object_list = object_list = this.object_lists.find(
          ol => ol.id == page_object.object_list_id
        );
      const env = {
        is_viewer: true,
        Helper,
        THREE,
        page_object_params: page_object,
        renderer: this.renderer,
        camera: this.camera,
        scene: this.scene,
        normalize_size_function: this.normalize_size
      };

      let load_promise = ObjectListLoad[object_list.name](env);

      return load_promise;
    },
    add(object) {
      let url_split = object.vars.url.split('/')
      this.added_projects.push({
        name: url_split[url_split.length-1],
        id: object.id
      })

      this.data_id = object.id;

      const element = document.createElement("div"),
        object_list = this.object_lists.find(
          ol => ol.id == object.object_list_id
        ),  
        component_props_data = {
          data_id: object.id,
          caption: "{caption} #{id}"
            .replace("{caption}", object_list.caption)
            .replace("{id}", object.id),
          vars: object.vars,
          allow_upload: object_list.allow_upload,
          rules: object_list.rules,
          behaviours: object.behaviours,
          _transform_scale_x: object.transform_scale_x,
          _transform_scale_y: object.transform_scale_y,
          _transform_scale_z: object.transform_scale_z,
          _transform_pos_x: object.transform_pos_x,
          _transform_pos_y: object.transform_pos_y,
          _transform_pos_z: object.transform_pos_z,
          _transform_rot_x: object.transform_rot_x,
          _transform_rot_y: object.transform_rot_y,
          _transform_rot_z: object.transform_rot_z,
          _transform_rot_w: object.transform_rot_w,
          object_list_id: object.object_list_id,
          editor: this.editor
        }

      const Constructor = Vue.extend(EditorPageObjectParams3);
      const component_instance = new Constructor({
          propsData: component_props_data
        });

      this.object_list = object_list;

      console.log(component_props_data);

      this.scene.add(component_instance.object3d);

      document.getElementById(this.objects_target_id).prepend(element);
      component_instance.$mount(element);

      this.page_object_params.set(object.id, component_instance);

      const load_promise = this.load(component_instance, object_list);
      load_promise.then(() => {
        this.scene.add(component_instance.helper);
        component_instance.preview_transform(); //update_helper();
      });

      console.log(load_promise);

      document.getElementById("tsd").appendChild(component_instance.$el);

      return load_promise;
    }
  }
};
</script>

