<template>
  <div class="popup-image-start">
    <div class="popup-content">
      <div class="top-block">
        <span>Add an Image</span>
        <span @click="close">×</span>
      </div>

      <div class="bottom-block">
        <div class="left-bottom">
          <span v-show="!is_file_selected" :style="{ fontSize: '3vh', fontWeight: '600' }">
            Upload your image file
          </span>

          <div class="content-left">
            <input type="file" id="image-inp" @change="loadFiles" :accept="file_accept" />
            <div id="output">
              <img v-if="preview_image_url" :src="preview_image_url" style="width: 300px;" />
            </div>

            <div :class="{block_button: is_file_selected, block_button_one: !is_file_selected}">
              <button
                type="button"
                @click="loadAndUseImage"
                v-show="show_upload"
                class="button-use"
              >Upload image</button>
              <button
                @click="useImage"
                v-show="show_change"
                class="button-use"
              >Use this image</button>
              <div @click="clickLoadFile" class="button-change">Change Image...</div>
            </div>

            <div v-show="!is_file_selected" class="block-bottom-inf">
              <span class="text-size" :style="{ marginTop: '20px' }">
                Supported formats:
                <b>{{ allow_upload.ext.join(', ').toUpperCase() }}</b>
              </span>

              <span v-if="allow_upload.maxSize" class="text-size">
                Max Size:
                <b>{{ Math.round(allow_upload.maxSize / (1024*1024)) }} mb</b>
              </span>

              <!-- <span class="text-size">
                {{ _("Have an unsupported file?") }}
                <a href="#" class="link">
                  <b>{{ _("Convert it here >") }}</b>
                </a>
              </span> -->
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Helper from "../../Scripts/modules/Helper.js";

export default {
  data() {
    const allow_upload_ext = this.allow_upload.ext || [];
    const data = {
      is_file_selected: false,
      file_blob: undefined,
      file_blob_url: undefined,
      url_image: this._value,
      show_change: false,
      show_upload: false,
      file_ext: "jpg",
      file_accept: allow_upload_ext.length
        ? allow_upload_ext.map(ext => `.${ext}`).join(", ")
        : "*/*"
    };
    return data;
  },
  props: [
    "object",
    "name",
    "_value",
    "put_url",
    "public_url",
    "public_user_url",
    "allow_upload",
    "rules",
    "editor",
    "getUrl"
  ],
  computed: {
    preview_image_url() {
      return this.is_file_selected ? this.file_blob_url : this.url_image;
    }
  },
  methods: {
    clickLoadFile() {
      document.getElementById("image-inp").click();
      this.show_change = false;
      this.show_upload = false;
    },

    close() {
      this.object();
      this.is_file_selected = false;
    },

    loadFile() {
      this.$refs.load_button.click();
    },

    async send_image(blob) {
      this.url_image = await Helper.storagePut(
        this.put_url,
        "marker-" + Date.now() + "." + this.file_ext,
        blob
      );

      this.show_change = !this.show_change;
      this.show_upload = !this.show_upload;

      return this.url_image;
    },

    loadFiles(event) {
      let file = event.target.files[0];

      if (!file) {
        this.is_file_selected = false;
        return;
      }

      if (!file.type.match("image.*")) {
        alert("Image only please....");
        this.is_file_selected = false;
        return;
      }

      this.is_file_selected = true;

      let reader = new FileReader();

      reader.onload = (() => {
        return e => {
          const blob = new Blob([new Uint8Array(e.target.result)], {
            type: file.type
          });
          this.file_blob = blob;
          this.file_ext = file.name.split(".").pop();
          this.file_blob_url = URL.createObjectURL(blob);

          this.show_upload = !this.show_upload;
        };
      })(file);
      reader.readAsArrayBuffer(file);
    },

    loadImage(onload) {
      if (this.is_file_selected) {
        const promise = this.send_image(this.file_blob);
        if (onload) promise.then(onload);
      }
    },
    useImage() {
      this.getUrl(this.url_image);
    },
    loadAndUseImage() {
      this.loadImage(() => this.useImage());
    }
  }
};
</script>

