<template>
    <editor-input-url3
        v-if="allow_upload"
        :name="name"
        v-on:input="value = $event"
        :_value="value"
        :put_url="storage_put_url"
        :public_url="editor.storage_info.container_url"
        :public_user_url="editor.storage_info.user_url"
        :allow_upload="allow_upload"
        :rules="rules"
        @input="change"
        :editor="editor"
        :id="id"
    ></editor-input-url3>
    <input
        v-else
        type="text"
        class="form-control"
        :name="name"
        v-model="value"
        @keydown="keydown($event)"
        @input="change"
    />
</template>

<script>
export default {
    props: [
        "editor",
        "_value",
        "var_name",
        "name",
        "storage_put_url",
        "allow_upload",
        "rules",
        "id"
    ],
    data() {
        return {
            value: typeof this._value == "undefined" ? "" : this._value
        };
    },
    computed: {
        inputListeners: function() {
            var vm = this;
            // `Object.assign` объединяет объекты вместе, чтобы получить новый объект
            return Object.assign(
                {},
                // Мы добавляем все слушатели из родителя
                this.$listeners,
                // Затем мы можем добавить собственные слушатели или
                // перезаписать поведение некоторых существующих.
                {
                    // Это обеспечит, что будет работать v-model на компоненте
                    input: function(event) {
                        vm.$emit("input", event.target.value);
                    }
                }
            );
        }
    },
    methods: {
        change() {
            this.$emit("input", this.value);
        },
        keydown(event) {
            if (event.keyCode === 13) {
                event.stopPropagation();
                event.preventDefault();
                this.change(event);
            }
        }
    }
};
</script>
