</template>

<script>
import Helper from "../../Scripts/modules/Helper.js";

export default {
  data() {
    return {};
  },
  props: ["id", "showhide", "visible", "form_acton", "onupdate"],
  methods: {
    close() {
      this.showhide();
    },
    submit(event) {
      if (event) event.preventDefault();

      Helper.axiosFormSubmit(document.forms["add_payment_method_form"])
        .then(response => {
          console.log("add pay meth resp", response);
          this.$root.d.user = response.data.user;
          this.close();
          if (typeof this.onupdate == "function") this.onupdate();
        })
        .catch(error => {
          // error.response.data
          const data_error =
            error.response && error.response.data ? error.response.data : null;
          if (data_error) {
            alert(
              "save data errors: " + JSON.stringify(data_error, null, "\t")
            );
          } else {
            alert("save network error: " + error);
          }
        });
    }
  }
};
</script>

