<template>
  <div class="form-group">
    <label>{{ var_name ["_", " "] }}:</label>

    <editor-var-input3
      :editor="editor"
      v-model="value"
      :_value="_value"
      :var_name="var_name"
      :name="
                'objects[' +
                    data_id +
                    '][behaviours][' +
                    behaviour.id +
                    '][vars][' +
                    var_name +
                    ']'
            "
      :storage_put_url="{
                type: 'BehaviourList',
                id: behaviour.behaviour_list_id,
                var_name
            }"
      :allow_upload="behaviour.allow_upload[var_name]"
      :rules="behaviour.rules[var_name]"
      @input="behaviour.vars[var_name] = $event"
    ></editor-var-input3>
  </div>
</template>

<script>
export default {
  props: ["behaviour", "data_id", "var_name", "_value", "var_rule", "editor"],
  data() {
    return {
      value: typeof this._value == "undefined" ? "" : this._value
    };
  },
  computed: {
    inputListeners: function() {
      var vm = this;
      // `Object.assign` объединяет объекты вместе, чтобы получить новый объект
      return Object.assign(
        {},
        // Мы добавляем все слушатели из родителя
        this.$listeners,
        // Затем мы можем добавить собственные слушатели или
        // перезаписать поведение некоторых существующих.
        {
          // Это обеспечит, что будет работать v-model на компоненте
          input: function(event) {
            vm.$emit("input", event.target.value);
          }
        }
      );
    }
  },
  created() {
    //
  },
  mounted() {
    //
  },
  methods: {},
  watch: {}
};
</script>
