<template>
  <div class="mb-2 px-2 py-2 custom-padding" id="object_list_add_controls">
    <div id="object_list_add">
      <h1 class="header-ar-content">AR content</h1>

      <div class="col-10 pr-1 button" v-for="object in object_lists" v-bind:key="object.id">
        <div
          data-toggle="collapse"
          role="button"
          class="btn btn-primary btn-block"
          :title="'Add'"
          :style="{ alignItems: 'center' }"
        >
          <div
            @click="showHide(object.id)"
            :style="{
                            zIndex: '1',
                            width: '100%',
                            alignItems: 'center',
                            textAlign: 'left',
                            textOverflow: 'ellipsis',
                            overflow: 'hidden',
                            textOverflow: 'ellipsis',
                            whiteSpace: 'nowrap'
                        }"
          >
            <img id="img-button" :src="'/images/button-image' + object.id + '.png'" alt />
            {{ object.caption }}
          </div>

          <span
            :id="object.id + 'div-add'"
            @click="togglePopup(object.id)"
            :style="{
                            float: 'right',
                            zIndex: '99',
                            height: '5vh',
                            width: '37px',
                            background: '#2a80c7',
                            borderRadius: '0 5px 5px 0'
                        }"
          >&#x271A;</span>
          <!-- @click="add(object.id, negative_id_couner, 'https://mywebar-a.akamaihd.net/56/215/Snimok_ekrana_2020-03-28_v_11.54.59.png')" -->
        </div>
        <div
          v-if="object_items.length"
          :id="'id-' + object.id"
          :style="{
                        display: 'flex',
                        flexDirection: 'column',
                        margin: '0 0 0 2vw'
                    }"
          :key="render_object_items"
        >
          <div
            v-for="object_item in object_items.filter(oi => object.id == oi.object_list_id)"
            :key="object_item.counter"
            :id="'del-'+object_item.counter"
            :style="'background: ' + (object_item_id == object_item.counter ? '#fff' : '#ccc') + '; cursor: pointer; display: flex; flex-direction: row; justify-content: space-between; margin: 1vh 0 1vh 0; border: 1px solid #ced4da; align-items: center; border-radius: 8px; padding-left: 0.8vh;'"
          >
            <a
              :data-name="object_item.name"
              :data-counter="object_item.counter"
              style="text-decoration: none; width: 100%; overflow: hidden; padding: 5px; text-overflow: ellipsis;"
              data-toggle="collapse"
              role="button"
              aria-expanded="false"
              :aria-controls="'object-' + object_item.counter + '-collapse'"
              @click="object_item_select(object_item)"
            >{{object_item.name}}</a>

            <div style="display: flex; align-items: center; padding: 0.4vh;">
              <div
                style="background: rgb(42, 128, 199); border-radius: 26px; width: 26px; height: 26px; align-items: center; justify-content: center; display: flex; padding: 0.8vh; margin: 0px 5px 0px 0px;"
                @click="object_item_change(object_item)"
              >
                <img src="" style="width: 14px;" />
              </div>
              <div
                style="border-radius: 26px; width: 26px; height: 26px; align-items: center; justify-content: center; display: flex; padding: 0.8vh; margin: 0px 5px 0px 0px;"
                @click="object_item_remove(object_item)"
              >
                <!-- <img src="../../images/remove.png" style="width: 28px;" /> -->
                <img src="" style="width: 28px;" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <factory-popup
      :editor="editor"
      type="image"
      name="test1"
      :object_list="getObjectListByName('PhotoRect')"
      var_name="url"
      :flag_show="flag_show_popup1"
      :toggle_callback="togglePopup1"
      :get_url_callback="getUrl1"
      _value
    ></factory-popup>

    <factory-popup
      :editor="editor"
      type="3d-model"
      name="test2"
      :object_list="getObjectListByName('ModelGLTF')"
      var_name="url"
      :flag_show="flag_show_popup2"
      :toggle_callback="togglePopup2"
      :get_url_callback="getUrl2"
      _value
    ></factory-popup>

    <factory-popup
      :editor="editor"
      type="video"
      name="test6"
      :object_list="getObjectListByName('VideoRect')"
      var_name="url"
      :flag_show="flag_show_popup6"
      :toggle_callback="togglePopup6"
      :get_url_callback="getUrl6"
      _value
    ></factory-popup>

    <factory-popup
      :editor="editor"
      type="video-sprite"
      name="test3"
      :object_list="getObjectListByName('VideoSprite2D')"
      var_name="url"
      :flag_show="flag_show_popup3"
      :toggle_callback="togglePopup3"
      :get_url_callback="getUrl3"
      _value
    ></factory-popup>
  </div>
</template>

<script>
// import * as THREE from "../devar/webar/vendor/three/build/three.module.js";

export default {
  props: ["target_selector", "object_lists", "_negative_id_couner", "editor"],
  mounted() {
    if (typeof this.negative_id_couner == "undefined")
      this.negative_id_couner = -1;

    // XXX s.yaglov 'url' var may not be hardcoded
    this.editor.page_objects.forEach(object =>
      this.object_item_add(
        object.object_list_id,
        object.id,
        object.vars.url ?? ""
      )
    );
  },
  data() {
    return {
      negative_id_couner: this._negative_id_couner,
      flag_show_popup1: false,
      flag_show_popup2: false,
      flag_show_popup3: false,
      flag_show_popup6: false,
      name: "Element",
      name_added_element: undefined,
      object_items: [],
      object_item_id: undefined
    };
  },
  methods: {
    getObjectListByName(name) {
      return this.object_lists.find(ol => ol.name == name);
    },
    getUrl1(url, name) {
      console.log(name);
      this.name_added_element = name;
      this.add(1, this.negative_id_couner, url);
      this.flag_show_popup1 = false;
    },
    getUrl2(url, name) {
      console.log(name);
      this.name_added_element = name;
      this.add(2, this.negative_id_couner, url);
      this.flag_show_popup2 = false;
    },
    getUrl3(url, name) {
      console.log(name);
      this.name_added_element = name;
      this.add(3, this.negative_id_couner, url);
      this.flag_show_popup3 = false;
    },
    getUrl6(url, name) {
      console.log(name);
      this.name_added_element = name;
      this.add(6, this.negative_id_couner, url);
      this.flag_show_popup6 = false;
    },
    togglePopup(id) {
      this["togglePopup" + id]();
    },
    togglePopup1() {
      this.flag_show_popup1 = !this.flag_show_popup1;
    },
    togglePopup2() {
      this.flag_show_popup2 = !this.flag_show_popup2;
    },
    togglePopup3() {
      this.flag_show_popup3 = !this.flag_show_popup3;
    },
    togglePopup6() {
      this.flag_show_popup6 = !this.flag_show_popup6;
    },
    showHide(id) {
      let obj = document.getElementById("id-" + id);
      if (obj.style.display == "flex") {
        obj.style.display = "none";
      } else {
        obj.style.display = "flex";
      }
    },
    object_item_add(object_list_id, counter, url) {
      const object_item = {
        object_list_id,
        counter,
        url,
        name: url.split("/").pop()
      };
      this.object_items.push(object_item);
      this.render_object_items++;
    },
    add(id, counter, url) {
      this.object_item_add(id, counter, url);

      const object_list_id = id,
        object_list = this.object_lists.find(o => o.id == object_list_id);

      this.rules = object_list.rule;
      this.allow_upload = object_list.allow_upload;

      this.editor.add({
        id: counter,
        object_list_id: object_list_id,
        vars: { url: url },
        rules: object_list.rule,
        allow_upload: object_list.allow_upload,
        transform_scale_x: object_list.transform_scale_x,
        transform_scale_y: object_list.transform_scale_y,
        transform_scale_z: object_list.transform_scale_z,
        transform_pos_x: object_list.transform_pos_x,
        transform_pos_y: object_list.transform_pos_y,
        transform_pos_z: object_list.transform_pos_z,
        transform_rot_x: object_list.transform_rot_x,
        transform_rot_y: object_list.transform_rot_y,
        transform_rot_z: object_list.transform_rot_z,
        transform_rot_w: object_list.transform_rot_w,
        behaviours: []
      });

      // XXX s.yaglov add play on click behaviour to video- elements
      if ([3, 6].indexOf(object_list_id) > -1) {
        setTimeout(() => {
          const params = this.editor.page_object_params.get(counter);
          params.$refs.add_behaviour.addByListId(2);
        }, 1);
      }

      this.negative_id_couner--;

      this.object_item_select({ url: url, name: this.name, counter: counter });
    },
    object_item_select(object_item) {
      const id = object_item.counter;
      const name = object_item.name;
      this.object_item_id = id;
      this.render_object_items++;

      this.editor.ele(name, id);

      document.getElementById("click_" + id + "_element").click();

      this.object_items.forEach(oi => {
        let obj = "object_" + oi.counter;

        if (object_item.counter == oi.counter) {
          document.getElementById(obj).style.display = "block";
          document.getElementById(obj + "button").style.display = "flex";
          document.getElementById(obj + "_collapse").style.display = "block";
        } else {
          document.getElementById(obj).style.display = "none";
        }
      });
    },
    object_item_change(object_item) {
      document
        .getElementById(object_item.counter + "-button-change-click")
        .click();
    },
    object_item_remove(object_item) {
      this.editor.remove(object_item.counter);

      const index = this.object_items.indexOf(object_item);
      this.object_items.splice(index, 1);

      this.object_item_id = undefined;
      this.render_object_items++;
    }
  }
};
</script>

