  <div class="load-3d-project">
      <div class="content p-3">
        <div class="header">
          <h5>Add a 3D Model</h5>
          <b><span @click="close" class="close">&times;</span></b>
        </div>
        <div class="text-info">
          <p>Upload a file from your device</p>
