        <div style="width: 100%; height: 30px;" class="mt-2">
          <p class="text-temp">Import file from the library</p>
        </div>

        <div class="block-info mt-2 p-2">
          <img style="width: 44%;" class="mb-1" src="../../assets/img/Devar.png" alt="">
          <p>Choose from the library including 
          over 1,000 AR-ready 3D models</p>
        </div>
      </div>
