</template>

<script>
import * as THREE from "../../Scripts/devar/webar/vendor/three/build/three.module.js";

import { OrbitControls } from "../../Scripts/devar/webar/vendor/three/3rdparty/jsm/controls/OrbitControls.js";
import { TransformControls } from "../../Scripts/devar/webar/vendor/three/3rdparty/jsm/controls/TransformControls.js";
Object.assign(THREE, { OrbitControls, TransformControls });

// import Helper from "../../Scripts/modules/Helper.js";

export default {
  name: 'load-3d-model',
  props: ['close', 'rules', 'allow_upload'],
  mounted() {
    const container = document.getElementById("scene");

    console.log(container.clientWidth, container.clientHeight)
    this.renderer.setSize(container.clientWidth, 170);

    container.appendChild(this.renderer.domElement);

    const color = 0xffffff;
    const intensity = 1;
    const light = new THREE.AmbientLight(color, intensity);
    this.scene.add(light);

    const controls = new THREE.OrbitControls(
      this.camera,
      this.renderer.domElement
    );
    controls.maxPolarAngle = Math.PI / 1;

    // const plane = new THREE.GridHelper(20, 40);
    // this.scene.add(plane);

    let planeGeometry = new THREE.PlaneGeometry(5, 5, 1, 1);
    let planeMaterial = new THREE.MeshBasicMaterial({ color: 0xffffff });
    let plane = new THREE.Mesh(planeGeometry, planeMaterial);
    plane.rotation.x = -0.5 * Math.PI;
    this.scene.add(plane);

    // 'https://mywebar-a.akamaihd.net/examples/Models/ABC/A.glb',

    this.renderer.setClearColor(0xe0e0e0, 1);
    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.outputEncoding = THREE.sRGBEncoding;
    this.renderer.shadowMap.enabled = true;

    this.camera.position.set(0, 2, 4);

    this.animate();

    window.scrollTo( 0, 0 );
  },
  data() {
    let data = {
      scene: new THREE.Scene(),
      camera: new THREE.PerspectiveCamera(
        75,
        window.innerWidth / window.innerHeight,
        0.1,
        1000
      ),
      renderer: new THREE.WebGLRenderer({ antialias: true, alpha: true }),
      managerShow: false,
      data: [],
      plan: undefined,
      data_format: 'GLB',
      data_size: '50'
    }
    return data;
  },
  methods: {
    choiseFile() {
      document.getElementById('input-file').click()
    },
    addUrl(url) {
      console.log(url)
    },
    getUrl(url) {
      console.log(url)
    },
    normalize_size(object3d) {
      console.warn("normalize size of ", object3d);
      let size3 = object3d.size3;
      if (!size3) {
          size3 = new THREE.Vector3();
          new THREE.Box3().setFromObject(object3d).getSize(size3);
      }
      const scale = 1 / Math.max.apply(this, size3.toArray());
      object3d.scale.setScalar(scale);
    },
    animate() {
      requestAnimationFrame(this.animate);
      this.renderer.render(this.scene, this.camera);
    },
  }
}
</script>

