<script>
import * as THREE from "../../Scripts/devar/webar/vendor/three/build/three.module.js";

export default {
  props: [
    "caption",
    "data_id",
    "vars",
    "allow_upload",
    "rules",
    "behaviours",
    "_transform_scale_x",
    "_transform_scale_y",
    "_transform_scale_z",
    "_transform_pos_x",
    "_transform_pos_y",
    "_transform_pos_z",
    "_transform_rot_x",
    "_transform_rot_y",
    "_transform_rot_z",
    "_transform_rot_w",
    "object_list_id",
    "editor"
  ],
  data() {
    const object3d = new THREE.Object3D();
    object3d.name = this.data_id;

    const helper = new THREE.BoxHelper(object3d, 0xffff00);

    // deb_preview_rot: _.debounce(this.preview_rot, 30),
    // deb_preview_pos: _.debounce(this.preview_pos, 30),
    // deb_preview_scale: _.debounce(this.preview_scale, 30),
    // deb_preview_vars: _.debounce(this.preview_vars, 500),
    const data = {
      transform_scale_x: parseFloat(this._transform_scale_x),
      transform_scale_y: parseFloat(this._transform_scale_y),
      transform_scale_z: parseFloat(this._transform_scale_z),
      transform_pos_x: parseFloat(this._transform_pos_x),
      transform_pos_y: parseFloat(this._transform_pos_y),
      transform_pos_z: parseFloat(this._transform_pos_z),
      transform_rot_x: parseFloat(this._transform_rot_x),
      transform_rot_y: parseFloat(this._transform_rot_y),
      transform_rot_z: parseFloat(this._transform_rot_z),
      transform_rot_w: parseFloat(this._transform_rot_w),
      object3d,
      helper,
      deb_preview_rot: this.preview_rot,
      deb_preview_pos: this.preview_pos,
      deb_preview_scale: this.preview_scale,
      deb_preview_vars: this.preview_vars,
      object: undefined,
      behaviour_vars: {},
      behaviour_vars_key: 0,
      self: this,

      selected: undefined,

      elem_changes: {
        1: {
          id: 1,
          change: [
            {
              id: 0,
              name: 'X',
              selected: false
            }, 
            {
              id: 1,
              name: 'Y',
              selected: false
            }, 
            {
              id: 2,
              name: 'Z',
              selected: false
            }
          ],
          selected: false
        },
        2: {
          id: 2,
          change: [
            {
              id: 0,
              name: 'X',
              selected: false
            }, 
            {
              id: 1,
              name: 'Y',
              selected: false
            }, 
            {
              id: 2,
              name: 'Z',
              selected: false
            }
          ],
          selected: false
        },
        3: {
          id: 3,
          change: [
            {
              id: 0,
              name: 'X',
              selected: false
            }, 
            {
              id: 1,
              name: 'Y',
              selected: false
            }, 
            {
              id: 2,
              name: 'Z',
              selected: false
            }
          ],
          selected: false
        },
        4: {
          id: 4,
          change: [
            
          ],
          selected: false
        }
      },

      block_scale: false,
      change_scale: false,
      select_changes_block: 0,
    };

    this.behaviours.forEach(behaviour => {
      this.resolve_behaviour_params(behaviour);
      data.behaviour_vars[behaviour.id] = behaviour.vars;
    });

    return data;
  },
  computed: {
    events: function() {
      if (this._cached_events) return this._cached_events;

      // const events = (this._cached_events = Object.keys(
      //   this.behaviour_lists_by_events
      // ));
      const events = ''

      return events;
    },
    behaviour_lists_by_events() {
      if (this._cached_behaviour_lists_by_events)
        return this._cached_behaviour_lists_by_events;

      const result = {};

      this.editor.behaviour_lists.reduce((by_events, behaviour_list) => {
        behaviour_list.event_set.forEach(event => {
          if (typeof by_events[event] == "undefined")
            by_events[event] = [behaviour_list];
          else by_events[event].push(behaviour_list);
        });
        return by_events;
      }, result);

      // this._cached_behaviour_lists_by_events = result;
      return result;
    },
    behaviour_lists_by_id() {
      if (this._cached_behaviour_lists_by_id)
        return this._cached_behaviour_lists_by_id;

      const result = {};

      this.editor.behaviour_lists.reduce((by_id, behaviour_list) => {
        by_id[behaviour_list.id] = behaviour_list;
        return by_id;
      }, result);

      // this._cached_behaviour_lists_by_id = result;
      return result;
    }
  },
  created() {},
  mounted() {
    this.preview_transform(true);
  },
  methods: {
    selectChanges(id) {      
      this.block_scale = true;
      this.selected = id;

      for (let elem in this.elem_changes) {
        if (elem == id) {
          this.elem_changes[elem].selected = true;
        } else {
          this.elem_changes[elem].selected = false;
        }
      }
    },
    back() {
      this.selected = undefined;

      for (let elem in this.elem_changes) {
        this.elem_changes[elem].selected = false;
      }
    },
    selectBlockChanges(id) {
      this.select_changes_block = id;

      this.elem_changes[this.selected].change.forEach(i => {
        if (i.id == id) {
          i.selected = true;
          document.getElementById('border_'+i.id).style.opacity = 1;
          document.getElementById('border_'+i.id).classList.add('activeBorder')
        } else {
          i.selected = false;
          document.getElementById('border_'+i.id).style.opacity = 0.4;
          document.getElementById('border_'+i.id).classList.remove('activeBorder')
        }
      })
    },
  



    range(event) {
      console.log(event.target.value);
    },
    resolve_behaviour_params(behaviour) {
      const behaviour_list = this.behaviour_list_by_id(
        behaviour.behaviour_list_id
      );
      Object.assign(behaviour, {
        rules: behaviour_list.rules,
        allow_upload: behaviour_list.allow_upload,
        vars: behaviour.vars || { ...behaviour_list.vars }
      });
    },
    add_behaviour(behaviour) {
      this.resolve_behaviour_params(behaviour);
      this.behaviours.push(behaviour);
      this.behaviour_vars[behaviour.id] = behaviour.vars;
      this.behaviour_vars_key++;
    },
    remove_behaviour(behaviour) {
      const index = this.behaviours.indexOf(behaviour);
      this.behaviours.splice(index, 1);
      this.behaviour_vars[behaviour.id] = undefined;
      this.behaviour_vars_key++;
    },
    highlight_helper() {
      // if (
      //   document
      //     .querySelector(event.target.attributes.href.value)
      //     .classList.contains("show")
      // ) {
      //   this.helper.material.color.setHex(0xffff00);
      // } else {
      //   this.helper.material.color.setHex(0xff0000);
      //   this.editor.transformControls.attach(this.object3d);
      //   transform_mode.style.display = "block";
      // }

      if (this.helper.material.color.getHex() != 16776960) {
        this.helper.material.color.setHex(0xffff00);
      } else {
        this.helper.material.color.setHex(0xff0000);
        this.editor.transformControls.attach(this.object3d);
        // transform_mode.style.display = "block";
      }

      //this.update_helper();
    },
    update_helper() {
      const findFirstChild = (o, f) => {
        //console.log("traverse to find helper_object_mesh", o);
        //console.log("has num childs ", o.children.length);
        if (o.children && o.children.length) {
          for (const c of o.children) {
            const result = f(c) || findFirstChild(c, f);
            if (typeof result !== "undefined") return result;
          }
        }
      };

      let {
        helper_object_mesh = undefined,
        helper_object_self = true
      } = this.object3d.userData;

      if (!helper_object_mesh) {
        helper_object_mesh = findFirstChild(this.object3d, o => {
          console.log("check to find helper_object_mesh");
          if (o.type == "Mesh" && o.name == "helper_object_mesh") {
            helper_object_self = false;
            return o;
          }
        });

        if (helper_object_self) {
          helper_object_mesh = this.object3d;
        } else {
          this.helper.setFromObject(helper_object_mesh);
        }

        Object.assign(this.object3d.userData, {
          helper_object_mesh,
          helper_object_self
        });
      }

      const update = () => {
        this.helper.update();
        ["rotateX", "rotateY", "rotateZ"].forEach((method, index) => {
          this.helper.geometry[method](-this.editor.rotations[index]);
        });
      };

      helper_object_self ? update() : setTimeout(update, 100);
    },
    remove() {
      if (
        confirm("Really delete {name}?".replace("{name}", this.caption))
      ) {
        this.editor.remove(this.data_id);
      }
    },
    empty() {
      if (
        typeof this.object == "object" &&
        typeof this.object.destroy == "function"
      )
        this.object.destroy();

      while (this.object3d.children.length > 0) {
        this.object3d.remove(this.object3d.children[0]);
      }
    },

    preview_transform(without_helper) {
      this.preview_scale(without_helper);
      this.preview_pos(without_helper);
      this.preview_rot(without_helper);
    },
    preview_scale(without_helper) {
      this.object3d.scale.set(
        this.transform_scale_x,
        this.transform_scale_y,
        this.transform_scale_z
      );
      if (!without_helper) this.update_helper();
    },
    preview_pos(without_helper) {
      this.object3d.position.fromArray([
        this.transform_pos_x,
        this.transform_pos_y,
        this.transform_pos_z
      ]);
      if (!without_helper) this.update_helper();
    },
    preview_rot(without_helper) {
      this.object3d.rotation.fromArray([
        this.transform_rot_x,
        this.transform_rot_y,
        this.transform_rot_z
      ]);
      if (!without_helper) this.update_helper();
    },
    preview_vars() {
      // XXX s.yaglov load new image
      this.empty();
      this.editor.load(this).then(() => {
        this.update_helper();
      });
    },
    behaviour_list_by_id(id) {
      const behaviour_list = this.editor.behaviour_lists.find(
        bl => bl.id == id
      );
      return behaviour_list;
    },
    behaviour_lists_by_object_list_id(object_list_id) {
      const object_list = this.editor.object_lists.find(
        ol => ol.id == object_list_id
      );
      return this.editor.behaviour_lists.filter(behaviour_list => {
        return (
          behaviour_list.target_object_type_id_set.indexOf(
            object_list.object_type_id
          ) > -1
        );
      });
    },
    page_objects_by_target_object_type_id_set(target_object_type_id_set) {
      return Array.from(this.editor.page_object_params.values()).filter(
        page_object_params => {
          const object_list = this.editor.object_lists.find(
            ol => ol.id == page_object_params.object_list_id
          );
          console.log(
            "page_objects_by_target_object_type_id_set: test ",
            { object_list },
            " of ",
            { page_object_params },
            " by ",
            { target_object_type_id_set }
          );
          return (
            target_object_type_id_set.indexOf(object_list.object_type_id) > -1
          );
        }
      );
    },
    page_objects_by_behaviour_list_id(behaviour_list_id) {
      let page_objects = [];
      if (behaviour_list_id) {
        const target_object_type_id_set = this.behaviour_lists_by_id[
          behaviour_list_id
        ].target_object_type_id_set;

        console.log("page_objects_by_behaviour_list_id: ", {
          target_object_type_id_set
        });

        page_objects = this.page_objects_by_target_object_type_id_set(
          target_object_type_id_set
        );
      }
      console.log("page_objects_by_behaviour_list_id: ", {
        page_objects
      });
      return page_objects;
    },
    page_objects_by_behaviour_list_value(element_id) {
      const element = document.getElementById(element_id);
      if (!element) {
        console.log(
          "page_objects_by_behaviour_list_value: element not found ",
          element_id
        );
        return [];
      }
      return this.page_objects_by_behaviour_list_id(element.value);
    }
  },
  watch: {
    /*
    behaviour_vars: {
      deep: true
    },*/
    // убрал из всех функцию параметры old_val, new_val. Тк на них vue ругался потому что не используются
    vars: {
      handler: function() {
        this.deb_preview_vars();
      },
      deep: true
    },
    transform_scale_x: function() {
      this.deb_preview_scale();
    },
    transform_scale_y: function() {
      this.deb_preview_scale();
    },
    transform_scale_z: function() {
      this.deb_preview_scale();
    },
    transform_pos_x: function() {
      this.deb_preview_pos();
    },
    transform_pos_y: function() {
      this.deb_preview_pos();
    },
    transform_pos_z: function() {
      this.deb_preview_pos();
    },
    transform_rot_x: function() {
      this.deb_preview_rot();
    },
    transform_rot_y: function() {
      this.deb_preview_rot();
    },
    transform_rot_z: function() {
      this.deb_preview_rot();
    },
    transform_rot_w: function() {
      this.deb_preview_rot();
    }
  }
};
</script>

