<template>
  <div
    class="modal fade bd-example-modal-xl"
    tabindex="-1"
    role="dialog"
    aria-labelledby="upgrade_plan_modal_label"
    aria-hidden="true"
    v-show="flag_show_internal"
  >
    <div class="modal-dialog modal-xl" role="directory">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title text-big" id="upgrade_plan_modal_label">
            <b>Welcome to MyWebAR!</b> You are currently using a limited free version that will only allow You to try basic features of our editor
            <br />Upgrade to one of the premium plans to unlock the full functionality of the MyWebAR platform:
          </h5>
          <button
            type="button"
            class="close"
            @click="close"
            data-dismiss="modal"
            aria-label="Close"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-lg-4" v-for="d in plans" :key="d.id">
              <div class="card border-primary border-2px mb-3 border-r-10px">
                <div class="card-header bg-transparent border-primary border-2px">
                  <div class="text-primary text-big-bold">
                    {{d.name}}
                    <div v-if="d.price>0" class="text-dark float-right">{{d.price}}/{{d.period}}</div>
                  </div>
                </div>
                <div class="card-body">
                  <!--
                  <div v-if="d.plan_id=='b4'" class="row">
                    <div class="col-12">
                      <div v-if="d.scans" class="card-text">
                        <b>Over {{d.scans}}</b> scans
                      </div>
                      <div v-if="d.limits.pages" class="card-text">
                        <b>Over {{d.limits.pages}}</b> projects
                      </div>
                      <div v-if="d.size" class="card-text">
                        <b>Over {{d.size}}</b> storage
                      </div>
                    </div>
                  </div>
                  -->
                  <div class="row">
                    <div class="col-6">
                      <div v-if="d.scans" class="card-text">
                        <b>{{d.scans}}</b> scans
                      </div>
                      <div v-if="d.limits.pages" class="card-text">
                        <b>{{d.limits.pages}}</b> projects
                      </div>
                      <div v-if="d.size" class="card-text">
                        <b>{{d.size}}</b> storage
                      </div>
                    </div>
                    <div class="col-6">
                      <div v-if="d.limits.ready_models" class="card-text">
                        <b>3D models</b> library
                      </div>
                      <div v-if="d.limits.teaching_aids" class="card-text">
                        <b>Teaching aids</b> library
                      </div>
                    </div>
                  </div>

                  <div class="text-right">
                    <button
                      v-if="['e1','t1'].indexOf(d.plan_id)>-1"
                      class="btn btn-secondary rounded-pill"
                      disabled
                      @click.prevent
                    >Included</button>
                    <button
                      v-else-if="d.price>0"
                      @click="go(d)"
                      class="btn btn-success rounded-pill"
                    >Choose</button>
                    <a
                      v-else
                      href="mailto:mywebar@devar.org"
                      class="btn btn-success rounded-pill"
                    >Contact us</a>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="row text-center text-big-bold">
            <a class="col-12 text-info" href="#" @click.prevent="close">Upgrade later</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["plans", "force_show"],
  data() {
    return {
      flag_show_internal: undefined
    };
  },
  mounted() {
    if (typeof this.force_show == "boolean") {
      this.flag_show_internal = this.force_show;
    } else if (
      typeof localStorage.welcome_plans_pupup_flag_show != "undefined"
    ) {
      this.flag_show_internal = JSON.parse(
        localStorage.welcome_plans_pupup_flag_show
      );
    } else {
      this.flag_show_internal = true;
    }
    this.$el.modal("handleUpdate");
  },
  watch: {
    flag_show_internal(val) {
      localStorage.welcome_plans_pupup_flag_show = val;
      this.$el.modal(val ? "show" : "hide");
    }
  },
  methods: {
    close() {
      this.flag_show_internal = false;
    },
    go(plan) {
      location.href = plan.view_url;
    }
  }
};
</script>

