if (!File.prototype.arrayBuffer) {
  File.prototype.arrayBuffer = function() {
    return new Promise(rv => {
      var reader = new FileReader();
      reader.onloadend = evt => rv(evt.target.result);
      reader.readAsArrayBuffer(this);
    });
  };
}

