import axios from "axios";
import VueCookies from 'vue-cookies'

const instanse = axios.create({
  baseURL: "https://test.mywebar.com/api/"
})

instanse.interceptors.request.use(
  function(request) {
    const token = VueCookies.get('AuthToken')

    const newConfig = {...request, headers: {...request.headers, 'Authorization': `Bearer ${token}`}}
    return newConfig
  }, 
  function(error) {
    return Promise.reject(error);
  });

instanse.interceptors.response.use(
  function(response) {
    let dataJson = JSON.parse(response.request.response)    
    
    if (response.config.url == 'login/') {
      if (dataJson.success) {
        VueCookies.set('AuthToken', dataJson.success.token)    
      }
    }
    return response
  }, 
  function(error) {
    if (error.response.status === 401) {
      VueCookies.remove('AuthToken')
    }
    return Promise.reject(error)
  });

export default instanse;